  border-radius: $border-radius;\r\n  @include dotted-shadow(secondary);\r\n  @include themed-border('', 1px, dashed, secondary);\r\n  @include themed(background-color, background);  \r\n  margin-bottom: $spacing-regular;\r\n}\r\n// TODO: make themed\r\n@mixin conic-clip($color1, $color2, $percentage, $size) {\r\n  background-clip: text;\r\n  -webkit-background-clip: text;\r\n  margin: $spacing-xxsmall 0;\r\n  color: transparent;\r\n  background-image: repeating-conic-gradient(\r\n    at 0% 100%,\r\n    #ee6c4d 0deg 5deg,\r\n    #3d5a80 10deg 15deg\r\n  );\r\n}\r\n\r\n@mixin tablet-width() {\r\n  @media only screen and (max-width: $desktop-breakpoint) {\r\n    @content;\r\n  }\r\n}\r\n\r\n@mixin mobile-width() {\r\n  @media only screen and (max-width: $tablet-breakpoint) {\r\n    @content;\r\n  }\r\n}",".list {\r\n  font-size: 15px;\r\n  li {\r\n    margin-bottom: $spacing-xsmall;\r\n    list-style-type: '';\r\n    span {\r\n      position: relative;\r\n      left: $spacing-xsmall;\r\n    }\r\n  }\r\n\r\n  [data-bullet-color=\"background\"] {\r\n    &::marker {\r\n      @include themed(color, secondary);\r\n    }\r\n  }\r\n\r\n\r\n  [data-bullet-color=\"text\"] {\r\n    &::marker {\r\n      @include themed(color, text);\r\n    }\r\n  }\r\n\r\n}",".animation-block {\r\n  width: 250px;\r\n  height: 50px;\r\n  position: absolute;\r\n  transform: rotate(90deg);\r\n  overflow: hidden;\r\n\r\n  @include mobile-width() {\r\n    display: none;\r\n  }\r\n\r\n  &__animation {\r\n    height: 100%;\r\n    position: absolute;\r\n    width: 0px;\r\n    animation: movement 4s $speed forwards;\r\n    @include dotted-background(text, transparent, 60%, 4px);\r\n    @include themed(color, background);\r\n    font-weight: 900;\r\n    font-size: 40px;\r\n  }\r\n\r\n  &::after {\r\n    content: '';\r\n    position: absolute;\r\n    @include themed-border('top', 26px, solid, background);\r\n    @include themed-border('bottom', 26px, solid, background);\r\n    @include themed-border('left'